</doc-preview>
<h3>代码</h3>
<pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">vc-viewer</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"viewer"</span> <span class="hljs-attr">:animation</span>=<span class="hljs-string">"animation"</span> <span class="hljs-attr">:timeline</span>=<span class="hljs-string">"timeline"</span> <span class="hljs-attr">:camera.sync</span>=<span class="hljs-string">"camera"</span> <span class="hljs-attr">@ready</span>=<span class="hljs-string">"ready"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">vc-layer-imagery</span>&gt;</span>
