<h2>常见问题</h2>
<ul>
<li><code v-pre="">VcViewer</code> 组件容器本身是一个空的块级元素，如果容器不定义高度，<code v-pre="">viewer</code>将渲染在一个高度为 0 不可见的容器内。</li>
<li>该项目是通过动态添加<code v-pre="">script</code>标签引入<code v-pre="">Cesium</code> 的，因此 <code v-pre="">VcViewer</code> 组件及其所有子组件的渲染是异步的。因此，请使用在组件的 <code v-pre="">ready</code> 事件来执行场景 API 加载完毕后才能执行的代码，不要试图在 Vue 自身的生命周期中调用 <code v-pre="">Cesium</code> 类，更不要在这些时机修改 model 层。但在<code v-pre="">2.0.1+</code>版本中可以通过<code v-pre="">ref</code>来获取组件的<code v-pre="">createPromise</code>对象来执行相关操作。</li>
</ul>
<h3>错误用法</h3>
<pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">vc-viewer</span> <span class="hljs-attr">:animation</span>=<span class="hljs-string">"animation"</span> <span class="hljs-attr">:camera</span>=<span class="hljs-string">"camera"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-viewer</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="language-javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-title function_">data</span>(<span class="hljs-params"></span>) {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">camera</span>: {
          <span class="hljs-attr">position</span>: {
            <span class="hljs-attr">lng</span>: <span class="hljs-number">104.06</span>,
            <span class="hljs-attr">lat</span>: <span class="hljs-number">30.67</span>,
            <span class="hljs-attr">height</span>: <span class="hljs-number">2000</span>
          },
          <span class="hljs-attr">heading</span>: <span class="hljs-number">360</span>,
          <span class="hljs-attr">pitch</span>: -<span class="hljs-number">90</span>,
          <span class="hljs-attr">roll</span>: <span class="hljs-number">0</span>
        },
        <span class="hljs-attr">animation</span>: <span class="hljs-literal">false</span>
      }
    },
    <span class="hljs-title function_">mounted</span>(<span class="hljs-params"></span>) {
      <span class="hljs-variable language_">this</span>.<span class="hljs-property">camera</span>.<span class="hljs-property">position</span>.<span class="hljs-property">lng</span> = <span class="hljs-number">116.46</span>
      <span class="hljs-variable language_">this</span>.<span class="hljs-property">camera</span>.<span class="hljs-property">position</span>.<span class="hljs-property">lat</span> = <span class="hljs-number">39.92</span>
      <span class="hljs-variable language_">this</span>.<span class="hljs-property">camera</span>.<span class="hljs-property">position</span>.<span class="hljs-property">height</span> = <span class="hljs-number">500</span>
      <span class="hljs-variable language_">this</span>.<span class="hljs-property">animation</span> = <span class="hljs-literal">true</span>
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
<h3>正确用法</h3>
<pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">vc-viewer</span> <span class="hljs-attr">ref</span>=<span class="hljs-string">"viewer"</span> <span class="hljs-attr">:animation</span>=<span class="hljs-string">"animation"</span> <span class="hljs-attr">:camera</span>=<span class="hljs-string">"camera"</span> <span class="hljs-attr">@ready</span>=<span class="hljs-string">"ready"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-viewer</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="language-javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-title function_">data</span>(<span class="hljs-params"></span>) {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">camera</span>: {
          <span class="hljs-attr">position</span>: {
            <span class="hljs-attr">lng</span>: <span class="hljs-number">104.06</span>,
            <span class="hljs-attr">lat</span>: <span class="hljs-number">30.67</span>,
            <span class="hljs-attr">height</span>: <span class="hljs-number">2000</span>
          },
          <span class="hljs-attr">heading</span>: <span class="hljs-number">360</span>,
          <span class="hljs-attr">pitch</span>: -<span class="hljs-number">90</span>,
          <span class="hljs-attr">roll</span>: <span class="hljs-number">0</span>
        },
        <span class="hljs-attr">animation</span>: <span class="hljs-literal">false</span>
      }
    },
    <span class="hljs-title function_">mounted</span>(<span class="hljs-params"></span>) {
      <span class="hljs-variable language_">this</span>.<span class="hljs-property">$refs</span>.<span class="hljs-property">viewer</span>.<span class="hljs-property">createPromise</span>.<span class="hljs-title function_">then</span>(<span class="hljs-function">(<span class="hljs-params">{Cesium, viewer}</span>) =&gt;</span> {
        <span class="hljs-variable language_">console</span>.<span class="hljs-title function_">log</span>(<span class="hljs-string">'viewer is loaded.'</span>)
      })
    },
    <span class="hljs-attr">methods</span>: {
      <span class="hljs-title function_">ready</span>(<span class="hljs-params">cesiumInstance</span>) {
        <span class="hljs-keyword">const</span> { <span class="hljs-title class_">Cesium</span>, viewer } = cesiumInstance
        <span class="hljs-comment">// 在这儿获取Cesium和viewer实例，再执行相关逻辑代码</span>
        <span class="hljs-variable language_">this</span>.<span class="hljs-property">camera</span>.<span class="hljs-property">position</span>.<span class="hljs-property">lng</span> = <span class="hljs-number">116.46</span>
        <span class="hljs-variable language_">this</span>.<span class="hljs-property">camera</span>.<span class="hljs-property">position</span>.<span class="hljs-property">lat</span> = <span class="hljs-number">39.92</span>
        <span class="hljs-variable language_">this</span>.<span class="hljs-property">camera</span>.<span class="hljs-property">position</span>.<span class="hljs-property">height</span> = <span class="hljs-number">500</span>
        <span class="hljs-variable language_">this</span>.<span class="hljs-property">animation</span> = <span class="hljs-literal">true</span>
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
